<template>
  <div class="login_container">
    <el-dialog
      title="提示"
      :visible.sync="centerDialogVisible"
      width="30%"
      center
    >
      <span style="font-size: 20px; color: red">请输入正确账号和密码!</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
    <img
      class="backImg"
      src="https://img-blog.csdnimg.cn/e90664acbb424877adfaa40ca21cd35b.jpeg"
      alt=""
    />
    <div class="login_box">
      <span class="title">在线读书阅读系统</span>
      <!-- 头像 -->
      <div class="avator_box">
        <img
          src="https://p.ananas.chaoxing.com/star3/240_130c/8a0d7e35ecbb20ec4b19f185eed0bc2b.jpg"
          alt
        />
      </div>
      <!-- 输入表单 -->
      <el-form
        label-width="0"
        class="login_form"
        ref="ruleForm"
        :model="ruleForm"
      >
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="登录" name="first">登录</el-tab-pane>
          <el-tab-pane label="注册" name="second">注册</el-tab-pane>
        </el-tabs>
        <!-- name -->
        <el-form-item
          prop="admin"
          :rules="[
            { required: true, message: '账号不能为空' },
            {
              required: true,
              pattern: /^\d{3,11}$/,
              message: '只能由数字组成，长度必须在3-11位',
            },
          ]"
        >
          <el-input
            prefix-icon="el-icon-user"
            v-model.number="ruleForm.admin"
            placeholder="请输入账号"
          ></el-input>
        </el-form-item>
        <!-- password -->
        <el-form-item
          prop="pass"
          :rules="[
            { required: true, message: '密码不能为空', trigger: 'blur' },
            {
              required: true,
              pattern: /^\w+$/,
              message: '密码由数字、字母或下划线组成',
              trigger: 'blur',
            },
            {
              required: true,
              max: 11,
              message: '长度不能大于11',
              trigger: 'blur',
            },
          ]"
        >
          <el-input
            type="password"
            :show-password="true"
            v-model="ruleForm.pass"
            placeholder="请输入密码"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <!--button -->
        <el-form-item class="btns">
          <el-button @click="adminForm('ruleForm')" type="primary"
            >登录</el-button
          >
          <el-button @click="resetForm('ruleForm')">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
 
<script>
export default {
  data() {
    return {
      ruleForm: {
        admin: "",
        pass: "",
      },
      centerDialogVisible: false,
      activeName: "first",
    };
  },
  methods: {
    adminForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const { admin, pass } = this.ruleForm;
          const res = await this.$http.get(
            `/users?account=${admin}&pass=${pass}`
          );
          if (res.length > 0) {
            this.$router.push("/home");
          } else {
            this.centerDialogVisible = true;
          }
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    handleClick(tab) {
      if (tab.index === "1") {
        this.$router.push("/register");
      }
    },
  },
};
</script>
 
<style lang="less" scoped>
.login_container {
  background-color: #2b4b6b;
}
.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.login_box .title {
  position: absolute;
  font-size: 40px;
  display: block;
  text-align: center;
  width: 100%;
  top: -50%;
  letter-spacing: 1rem;
  font-weight: bold;
  background-image: linear-gradient(
    to right,
    rgba(255, 0, 0, 0.495),
    pink,
    #ccc,
    #ddd,
    #fff
  );
  background-clip: text;
  opacity: 0.8;

  color: transparent;
}
.avator_box {
  height: 130px;
  width: 130px;
  border: 1px solid #eee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  img {
    height: 100%;
    width: 100%;
    border-radius: 50%;
    background-color: #eee;
  }
}
.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}
.btns {
  display: flex;
  justify-content: flex-end;
}
.backImg {
  width: 100%;
  height: 100%;
}
</style>