import Vue from 'vue'
import App from './App.vue'
import './plugins/http'
import router from './router'
import { Button, Row, Col, Menu, Submenu, MenuItemGroup, MenuItem, Form, FormItem, Input, Dialog, Tabs, TabPane, Carousel,CarouselItem } from 'element-ui'

Vue.config.productionTip = false

Vue.use(Button)
Vue.use(Row)
Vue.use(Col)
Vue.use(Menu)
Vue.use(Submenu)
Vue.use(MenuItemGroup)
Vue.use(MenuItem)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Input)
Vue.use(Dialog)
Vue.use(Tabs)
Vue.use(TabPane)
Vue.use(Carousel)
Vue.use(CarouselItem)

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
