<template>
  <div v-if="computers" class="main-list">
    <!-- <MainItem /> -->
    <ul>
      <MainItem
        v-for="computerItem in computers"
        :key="computerItem.id"
        :computerItem="computerItem"
      />
    </ul>
  </div>
  <img class="loadingImg" v-else src="http://static.oschina.net/uploads/img/201409/26073953_DP0i.gif" alt="">
</template>

<script>
import MainItem from "./MainItem.vue";
/* import MainItem from "./MainItem.vue" */

export default {
  components: { MainItem },
  /* components: { MainItem } */
  data() {
    return {
      computers: null,
    };
  },
  created() {
    setTimeout(async () => {
      const res = await this.$http.get("/computer");
      this.computers = res;
    }, 300);
  }
};
</script>

<style>
.main-list {
  position: relative;
  flex-grow: 1;
  margin-left: 3%;
  padding: 30px;
  background-color: #f2f4f7;
}
.main-list>ul {
  position: absolute;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  border-radius: 6px;
  background-color: #f2f4f7;
  box-shadow: 0 0 10px #fff;
}
.main-list a {
  display: block;
}
.main-list a img {
  border-radius: 20px;
  overflow: hidden;
  opacity: 0.6;
}
.main-list a span {
  display: block;
  margin: 10px 0 16px 16px;
  color: #000;
  font-weight: bold;
}
.main-list a p {
  text-align: right;
  font-size: 12px;
}
</style>