<template>
  <div v-if="theories" class="main-list">
    <!-- <MainItem /> -->
    <ul>
      <MainItem
        v-for="computerItem in theories"
        :key="computerItem.id"
        :computerItem="computerItem"
      />
      <div
        style="
          width: 240px;
          height: 130px;
          margin-right: 80px;
          visibility: none;
        "
      ></div>
    </ul>
  </div>
  <img
    class="loadingImg"
    v-else
    src="http://static.oschina.net/uploads/img/201409/26073953_DP0i.gif"
    alt=""
  />
</template>

<script>
import MainItem from "./MainItem.vue";
export default {
  components: { MainItem },
  data() {
    return {
      theories: null,
    };
  },
  created() {
    setTimeout(async () => {
      const res = await this.$http.get("/computer?type=theory");
      this.theories = res;
    }, 300);
  },
  /* watch: {
    async $route(newValue) {
      console.log(newValue.params);
      const res = await this.$http.get("/computer?type=theory");
      this.theories = res;
      console.log(res);
    },
  }, */
};
</script>

<style>
.loadingImg {
    width: 100%;
    opacity: .6
}
</style>