import axios from 'axios'
import Vue from 'vue'
const http = axios.create({
    baseURL: 'http://localhost:3008',
    timeout: 1000
})

//响应拦截器
http.interceptors.response.use(res => {
    return res.data
})

Vue.prototype.$http = http

// 在 vue 全局下（整个项目中）都可能会使用这个 http 请求
// 直接使用 Vue.prototype.$http = http 属性名一般用 $ 做前缀
// 这样 vue 的原型中就拥有了 http 请求，那么vue实例就可以直接访问 http 了
// 组件内直接使用 this.$http 就是http
// 需要将该文件运行才能在全局使用 this.$http
// 那么需要引入到 main.js