import Vue from "vue";
import VueRouter from "vue-router"
import BookMainList from '../views/BookMainList.vue'
import BookHome from '../views/BookHome.vue'
import ComputerTheory from '../views/ComputerTheory.vue'
import ComputerPractice from '../views/ComputerPractice.vue'
import LoginDemo from '../views/LoginDemo.vue'
import RegisterDemo from '../views/RegisterDemo.vue'
import BookNo from '../views/BookNo.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        component: LoginDemo,
        meta: { type: false}
    },
    {
        path: '/register',
        component: RegisterDemo,
        meta: { type: false}
    },
    {
        path: '/home',
        component: BookHome,
        meta: { type: true}
    },
    {
        path: '/theories',
        component: ComputerTheory,
        meta: { type: true}
    },
    {
        path: '/practices',
        component: ComputerPractice,
        meta: { type: true}
    },
    {
        path: '/bookNo',
        component: BookNo,
        meta: { type: true}
    },
    {
        path: '/register',
        component: RegisterDemo,
        meta: { type: true}
    },
    {
        path: '/computerAll',
        component: BookMainList,
        meta: { type: true }
    }
]
const router = new VueRouter({
    routes,
    mode: 'hash'
})

export default router